var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"aboutMe"},[_c('div',{staticClass:"briefCtnr"},[_c('div',{staticClass:"text"},[_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("經過好幾年的時間依然還沒進到網頁設計的領域，但我仍然沒有放棄。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("我的本科系是產品設計。第一份工作做過基本的平面設計後，一直覺得自己的個性還有喜好，適合前端網頁設計。在視覺、使用者介面，與前端技術、解決問題，之間，找到我可以發揮的地方。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("我自認為我的的成長速度不快，但是我持續在成長。有很長一段時間我都在原地踏步，甚至浪費時間摸索，但是我想這就是我生命中歷經的過程，我歷史的一部分，這些過去的摸索都是有意義的，依然在潛意識的影響我現在 (甚至未來) 的思想，和決定。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("我曾經花了很長的時間，摸索人生的意義，但我一直找不到答案。原來意義是每個人自己去賦予在事物上的，對於一件事物的主觀看法。事物本身並沒有意義，意義是每個人自己賦予上去的。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("於是我開始有一個念頭，我想要做有意義的事情。我想在以後把前端網頁設計的能力，用來做對社會有貢獻的事情。我在 2019 上半年，嘗試透過各種網路資訊，去找需要 \"資訊志工\" 的地方。我幸運找到一個人，參與過他 (他與其他人) 的一個專案，把自己的能力貢獻給社會，不浪費時間。沒想到參與專案的過程中，我也見到了真實專案的複雜程度，大幅打開了視野，學到了很多東西。但礙於要先找工作的壓力，目前我把重點放在工作上。希望未來能把前端技能用在志工工作上。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("我這些年來最重視的價值，也是希望能持續維持的價值是: 坦白、透明。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("我的個性內向，我喜歡聽音樂，學習音樂編曲，思考簡單的哲學問題和人生的意義。")])]),_c('p',[_c('i',{staticClass:"defaultText defaultTextColor"},[_vm._v("想要更了解我每天的生活作息，動態 (2019/03 ~ 08)，可以翻看 "),_c('a',{attrs:{"href":"https://treegb.github.io/treegbBlog/19/03/18/190318m144719/"}},[_vm._v("我的這個 Blog")]),_vm._v(" 裡面紀錄著我剛進入前端工程師前的那段時間，每天的詳細作息。")])])]),_c('div',{staticClass:"img"})])])}]

